import React, { useState } from "react";
import { BrowserRouter, Switch, Route, Redirect } from "react-router-dom";
import ComingSoon from "./screens/ComingSoon/ComingSoon";
import Home from "./screens/Home/Home";
import RegionSearch from "./screens/RegionSearch/RegionSearch";
import Venue from "./screens/Venue/Venue";
import Login from "./screens/Login/Login";
import { AuthContext } from "./context";

export default function App() {
  const [isLoggedIn, setIsLoggedIn] = useState(false); //Overide logging

  const login = () => {
    setIsLoggedIn(true);
  };
  const logout = () => {
    setIsLoggedIn(false);
  };

  return (
    <AuthContext.Provider
      value={{
        isLoggedIn: isLoggedIn,
        login: login,
        logout: logout,
      }}
    >
      <Login />
      <BrowserRouter>
        {/* <Route path="/"> */}
        <Switch>
          {isLoggedIn ? (
            <>
              <Redirect to="/" />
              <Route exact path="/:region/:venue" component={Venue} />
              <Route exact path="/:region" component={RegionSearch} />
              <Route exact path="/" component={Home} />
            </>
          ) : (
            <ComingSoon />
          )}
        </Switch>
        {/* </Route> */}
      </BrowserRouter>
    </AuthContext.Provider>
  );
}
