import React from "react";
import { Link } from "react-router-dom";

import {
  Paper,
  Button,
  MenuItem,
  Select,
  Typography,
  FormControl,
  InputLabel,
  CardMedia,
  Card,
  Divider,
  Radio,
  FormControlLabel,
} from "@material-ui/core";

import { useStyles } from "./styles";

//components
import Rating from "../../components/Rating";

//images
import { queenstownBarsData } from "../../assets/images/queenstown/bars";

import Logo from "../../assets/images/logo.png";
import LogoBackground from "../../assets/images/logoBackground.png";
import banner from "../../assets/images/banner.jpg";
import data from "../../data.js";

export default function RegionSearch(props) {
  const classes = useStyles();
  console.log(data);
  console.log(data.region);
  // const town = data.find((x) => x.key === props.match.params.region);

  const town = props.match.params.region;
  const barsData = queenstownBarsData;
  const subRegions = [
    "ArrowTown",
    "Arthurs Point",
    "Frankton",
    "Gibbston",
    "Lake Hayes",
    "Queenstown",
  ];

  // populate locations
  let LocationsList = [];

  for (const region of subRegions) {
    LocationsList.push(<MenuItem>{region}</MenuItem>);
    // console.log(key, LocationsKey[key]);
  }

  //populate date for drop down
  var dateFormat = require("dateformat");
  let Dates = [];
  Dates.push(<MenuItem value={0}>Today</MenuItem>);
  for (let i = 1; i < 10; i++) {
    // console.log(dateFormat(now, "dd / mm"));
    let date = new Date(Date.now() - i * 24 * 60 * 60 * 1000);
    Dates.push(<MenuItem>{dateFormat(date, "ddd dd mmm")}</MenuItem>);
  }

  //populate date of the week for bookings
  let Bookings = [];
  for (let i = 0; i <= 6; i++) {
    let date = new Date(Date.now() + i * 24 * 60 * 60 * 1000);
    Bookings.push({ day: dateFormat(date, "ddd"), date: dateFormat(date, "dd/mm") });
  }

  console.log(Bookings);
  return (
    <div className="column homeLayout">
      {/* <AppBar color="transparent" className="column"> */}
      <div className={classes.logoWrapper}>
        {/* <Typography variant="h1" >1stdrinks.com</Typography> */}
        <Link to="/">
          <img className={classes.logo} src={Logo} alt="" />
        </Link>
      </div>
      <div>
        <Button className={classes.iconButton} variant="contained" color="secondary">
          Bar
        </Button>
        <Button className={classes.iconButton} variant="contained" color="secondary">
          Coffee
        </Button>
        <Button className={classes.iconButton} variant="contained" color="secondary">
          Healthy
        </Button>
      </div>

      {/* <Toolbar className="row">
        <MenuItem className="column">
          <BarIcon />
          <Typography>Bar</Typography>
        </MenuItem>
        <MenuItem className="column">
          <TabIcon />
          <Typography>Tab</Typography>
        </MenuItem>
        <MenuItem className="column">
          <CafeIcon />
          <Typography>Coffee</Typography>
        </MenuItem>
      </Toolbar> */}
      {/* </AppBar> */}
      <Paper elevation={3}>
        <Typography className={classes.town} variant="h1">
          {town}
        </Typography>
      </Paper>
      <div>
        <img className={classes.banner} src={banner} alt="" />
      </div>
      <div className="row">
        <Paper className={classes.paperForm}>
          <FormControl className={classes.formControl}>
            <InputLabel>Date</InputLabel>
            <Select
              MenuProps={{
                anchorOrigin: {
                  vertical: "bottom",
                  horizontal: "left",
                },
                transformOrigin: {
                  vertical: "top",
                  horizontal: "left",
                },
                getContentAnchorEl: null,
              }}
              defaultValue={0}
            >
              {Dates}
            </Select>
          </FormControl>
          <FormControl className={classes.formControl}>
            <InputLabel>Location</InputLabel>
            <Select
              MenuProps={{
                anchorOrigin: {
                  vertical: "bottom",
                  horizontal: "left",
                },
                transformOrigin: {
                  vertical: "top",
                  horizontal: "left",
                },
                getContentAnchorEl: null,
              }}
            >
              {LocationsList}
            </Select>
          </FormControl>
          <FormControl className={classes.formControl}>
            <InputLabel>Time</InputLabel>
            <Select
              MenuProps={{
                anchorOrigin: {
                  vertical: "bottom",
                  horizontal: "left",
                },
                transformOrigin: {
                  vertical: "top",
                  horizontal: "left",
                },
                getContentAnchorEl: null,
              }}
            >
              <MenuItem>Morning</MenuItem>
              <MenuItem>Afternoon</MenuItem>
              <MenuItem>Evening</MenuItem>
            </Select>
          </FormControl>
          <FormControl className={classes.formControl}>
            <Button variant="contained" color="primary">
              Find a venue
            </Button>
          </FormControl>
        </Paper>
      </div>

      <div className={classes.content}>
        <div>
          {barsData.map((venue) => (
            <Paper className={classes.paperCard} elevation={3}>
              <Card>
                <div className="row">
                  <div className={classes.cardImageContainer}>
                    <CardMedia image={venue.image} classes={{ root: classes.cardImageRoot }} />
                  </div>
                  <div className={`column ${classes.bookingContent}`}>
                    <Typography className={classes.venueTitle} variant="h5">
                      {venue.title}
                    </Typography>
                    <Rating
                      rating={Math.round((Math.random() + 2) * 1.5)}
                      numReviews={Math.round(Math.random() * 20)}
                    />
                    <div className="row">
                      {venue.subLocation} <span>{venue.cuisine}</span>
                    </div>
                    <div className="row">
                      {Bookings.map((item) => (
                        <div
                          className={`column ${classes.bookingDay} ${
                            Math.round(Math.random()) === 0 ? "active" : ""
                          }`}
                        >
                          <span>{item.day}</span>
                          <Divider variant="middle" />
                          <span>{item.date}</span>
                          <span>6PM</span>
                        </div>
                      ))}
                    </div>
                  </div>
                </div>
              </Card>
            </Paper>
          ))}
        </div>

        <FormControl classes={{ root: classes.option }}>
          <FormControlLabel control={<Radio />} label="Bar" />
          <FormControlLabel control={<Radio />} label="Craft Beer" />
          <FormControlLabel control={<Radio />} label="Cocktail" />
          <FormControlLabel control={<Radio />} label="Lounge" />
          <FormControlLabel control={<Radio />} label="Karaoke" />
          <FormControlLabel control={<Radio />} label="Live Band" />
          <FormControlLabel control={<Radio />} label="Parking" />
          <FormControlLabel control={<Radio />} label="Pet Friendly" />
          <FormControlLabel control={<Radio />} label="Pokies" />
          <FormControlLabel control={<Radio />} label="Sports Bar" />
          <FormControlLabel control={<Radio />} label="Pool Table" />
          <FormControlLabel control={<Radio />} label="Winery" />
        </FormControl>
      </div>
    </div>
  );
}
